<template>
  <div class="subaccount_wrapper">
    <div class="subaccount_loading" v-show="loading"></div>
    <div v-show="!loading" class="subaccount_content_wrapper">
      <el-table :data="subAccountList" style="width: 98%" height="100%">
        <el-table-column
          prop="username"
          :label="$t('subAccount.username')"
          width="width"
        >
        </el-table-column>
        <el-table-column
          prop="password"
          :label="$t('subAccount.password')"
          width="width"
        >
        </el-table-column>
        <el-table-column
          prop="prop"
          :label="$t('subAccount.action')"
          width="width"
        >
          <template slot-scope="scope">
            <el-row :gutter="6">
              <el-col :span="12">
                <el-button
                  size="small"
                  type="primary"
                  style="width: 80px"
                  @click="handleModify(scope)"
                  >{{ $t("btn.modify") }}</el-button
                >
              </el-col>
              <el-col :span="12">
                <el-button
                  :loading="
                    dltLoading[scope.$index] && dltLoading[scope.$index].loading
                  "
                  size="small"
                  type="primary"
                  style="width: 80px"
                  plain
                  @click="handleDelete(scope)"
                  >{{ $t("btn.delete") }}</el-button
                >
              </el-col>
            </el-row>
          </template>
        </el-table-column>
      </el-table>
      <pagination
        :total="total"
        :page.sync="listQuery.page"
        :limit.sync="listQuery.limit"
        @pagination="getList"
      ></pagination>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import { getSubStoreList } from "@/api/userAPI.js";
import { startLoading, endLoading } from "@/util/loading.js";
import Pagination from "@/components/Pagination";
export default {
  mounted() {
    this.getSubAccountPage();
  },
  data() {
    return {
      total: 0,
      subAccountList: [],
      listQuery: {
        page: 1,
        limit: 20,
      },
      dltLoading: [],
      loading: false,
    };
  },
  methods: {
    getList() {
      // page:page,limit: limit
      // 获取数据
      this.getSubAccountPage();
    },

    // 获取子账户列表
    getSubAccountPage() {
      this.loading = true;
      startLoading(".subaccount_loading");
      getSubStoreList({
        limit: this.listQuery.limit,
        offset: (this.listQuery.page - 1) * this.listQuery.limit,
      }).then((ret) => {
        this.loading = false;
        setTimeout(() => {
          endLoading();
        }, 0);
        this.total = ret.data.total;
        this.subAccountList = ret.data.list;
        this.dltLoading = this.getLoadingArray();
      });
    },
    getLoadingArray() {
      let array = new Array(this.subAccountList.length);
      let dltLoadingArray = array.fill({
        loading: false,
      });
      return dltLoadingArray;
    },
    handleModify(scope) {
      this.$router.push({
        name: "Add subAccount",
        params: scope.row,
      });
    },
    handleDelete(scope) {
      this.dltLoading.splice(scope.$index, 1, {
        loading: true,
      });
      let obj = {
        username: scope.row.username,
      };
      this.deleteSubAccount(obj)
        .then(() => {
          this.$message.success(this.$t("public.deleteSuccessMsg"));
          this.dltLoading.splice(scope.$index, 1, {
            loading: false,
          });
          this.subAccountList.splice(scope.$index, 1);
        })
        .catch(() => {
          this.dltLoading.splice(scope.$index, 1, {
            loading: false,
          });
          this.$message.error(this.$t("error.serverErr"));
        });
    },
    ...mapActions({
      getSubAccount: "user/getSubAccount",
      deleteSubAccount: "user/deleteSubAccount",
    }),
  },
  components: {
    Pagination,
  },
};
</script>

<style lang="scss" scoped>
.subaccount_loading {
  padding: 3em;
}

.subaccount_content_wrapper {
  height: calc(100vh - 250px);
}
</style>
